import {Component, Input, OnInit} from '@angular/core';
import {Observable, Subject, Subscription} from "rxjs";
import {debounceTime} from "rxjs";
import {IResponse} from "../../models/IResponse";
import {IUser_Object} from "../../models/IUser_Object";
import {UserService} from "../../services/user.service";
import {IUser} from "../../models";
import {ActivatedRoute} from "@angular/router";
import {Form, FormControl, FormGroup} from "@angular/forms";

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.css'],
  host: {
    class: 'display-contents'
  }
})
export class SearchBarComponent implements OnInit {


  private inputChanged: Subject<IUser_Object[]> = new Subject<IUser_Object[]>()
  private subscription: Subscription;

  _searchBar:FormGroup;
  _debounceTime = 1000
  _bar_info: string = ''
  _rData: IUser_Object[]

  constructor(
    private UserService: UserService,
  ) {
  }

  ngOnInit(): void {

    this._creatSearchBarForm()

    this.subscription = this.inputChanged
      .pipe(
        debounceTime(this._debounceTime)
      )
      .subscribe(() => {
        this.getBarInfo()
      })
  }

  inputInfoAdded(): void {
    this.inputChanged.next(this._rData)
  }

  getBarInfo() {
    this.UserService.getUsersIdPairs().subscribe(value => {
      console.log(value.results)
      this._rData = value.results
    })
  }

  _creatSearchBarForm(): void {
    this._searchBar = new FormGroup({
      search_bar_input: new FormControl('ore.kitto@gmail.com'),
    })
  }


  //Change input value to needed
  _chooseUser(){
    this._searchBar.controls['search_bar_input'].setValue('me')
  }

}

