import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {IUser_Object} from '../models/IUser_Object';
import {doctors_urls} from "../constants/base_urls";
import {BehaviorSubject, Observable} from "rxjs";
import {DoctorWithIdPairs} from "../models/DoctorWithIdPairs";
import {IPaginated_obj} from "../models/IPaginated_obj";

@Injectable({
  providedIn: 'root'
})
export class DoctorsService {

  constructor(
    private HttpClient: HttpClient
  ) {
  }

  deleteDoctorNum: BehaviorSubject<number> = new BehaviorSubject(0)
  docInfo: BehaviorSubject<Partial<IUser_Object>> = new BehaviorSubject<Partial<IUser_Object>>({})

  getAllDoctors(page_size: number = 5): Observable<IPaginated_obj<IUser_Object[]>> {
    return this.HttpClient.get<IPaginated_obj<IUser_Object[]>>(`${doctors_urls.allDoctors}`, {params: {page_size: page_size}})
  }

  getDoctorByID(id: number): Observable<IPaginated_obj<IUser_Object[]>> {
    return this.HttpClient.get<IPaginated_obj<IUser_Object[]>>(`${doctors_urls.doctorById}/${id}`)
  }

  doctorsWithIdPairs(): Observable<DoctorWithIdPairs[]> {
    return this.HttpClient.get<DoctorWithIdPairs[]>(`${doctors_urls.doctorsIdPairs}`)
  }

  doctorsWithPatients(): Observable<any> {
    return this.HttpClient.get(`${doctors_urls.doctorsWithListOfPatients}`)
  }

  addPatientToDoctor(PatientId: string): Observable<any> {
    return this.HttpClient.post(`${doctors_urls.addPatientToDoctor}`, PatientId)
  }

  addToDoctors(UserId: string):Observable<IPaginated_obj<IUser_Object>> {
    let userObj = {user_id:parseInt(UserId)}
    return this.HttpClient.post<IPaginated_obj<IUser_Object>>(`${doctors_urls.addUserToDoctors}`, userObj)
  }

  deleteDoctorById(id: number) {
    return this.HttpClient.delete(`${doctors_urls.deleteUserFromDoctor}/${id}`)
  }


}
