import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup} from "@angular/forms";
import {AuthService} from "../../services";
import {TokenStorageService} from "../../services/token-storage.service";
import {IAccessTokenInterface} from "../../models";
import {Router} from "@angular/router";
import {Is_authenticated} from "../../services/is_authenticated";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  auth: FormGroup;
  errMsg: string = ""
  isLoginFailed: boolean = false


  constructor(
    private authService: AuthService,
    private TokenStorage: TokenStorageService,
    private Router: Router,
    private isAuthObs: Is_authenticated

  ) {


  }

  ngOnInit(): void {
    this._createLoginForm()

  }

  _createLoginForm(): void {
    this.auth = new FormGroup({
      email: new FormControl('ore.kitto@gmail.com'),
      password: new FormControl('1111')
    })
  }

  LogInOk = () => {
    this.isAuthObs.userIsLogged.next(true)
    this.isLoginFailed = false
  }
  LogInFailed = (err: string) => {
    this.errMsg = err
    setTimeout(() => {
      this.errMsg = ''
    }, 3000)
    this.isLoginFailed = true
  }

  _login(): void {

    const tokenStorage = this.TokenStorage;
    const LogInFailed = this.LogInFailed;
    const LogInOk = this.LogInOk;
    let router: Router;
    router = this.Router;

    this.authService.login(this.auth.getRawValue()).subscribe({
      next(token: IAccessTokenInterface) {
        tokenStorage.saveTokens(token)
        LogInOk()
        router.navigate(['main']).then(()=>{})
      },
      error(err: { error: { detail: string; }; }) {
        LogInFailed(err.error.detail);
      }
    })
  }
}
