import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Observable} from "rxjs";
import {IPaginated_obj} from "../models/IPaginated_obj";
import {IUser_Object} from "../models/IUser_Object";
import {user_urls} from "../constants/base_urls";

@Injectable({
  providedIn: 'root'
})
export class UserService {

  constructor(
    private HttpClient: HttpClient
  ) {
  }

  getUserByID(id: number): Observable<IPaginated_obj<IUser_Object>> {
    return this.HttpClient.get<IPaginated_obj<IUser_Object>>(`${user_urls.getUserById}/${id}`)
  }

  getUsersIdPairs(searchingParams:{}={}): Observable<IPaginated_obj<IUser_Object[]>> {
    return this.HttpClient.get<IPaginated_obj<IUser_Object[]>>(`${user_urls.getUsersIdPairs}`, {params:{...searchingParams}})
  }
}
