import {Component, OnInit} from '@angular/core';
import {AuthService} from "./services";
import {Is_authenticated} from "./services/is_authenticated";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'health-care';

  constructor(
    private isAuthToken: AuthService,
    private isAuthObs: Is_authenticated
  ) {
  }

  ngOnInit() {
    let isAuth:boolean = this.isAuthToken.isAuthorization()
    this.isAuthObs.userIsLogged.next(isAuth)
  }
}
