import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {LoginComponent} from "./components/login/login.component";
import {SignupComponent} from "./components/signup/signup.component";
import {MainPageComponent} from "./components/main-page/main-page.component";
import {DoctorsComponent} from "./components/doctors-titles/doctors.component";
import {DoctorComponent} from "./components/doctors-titles/doctor/doctor.component";
import {AddDoctorComponent} from "./components/doctors-titles/add-doctor/add-doctor.component";

const routes: Routes = [
  // {path: 'main', component: MainPageComponent,},
  {path: 'login', component: LoginComponent,},
  {path: 'signUp', component: SignupComponent,},
  {
    path: 'doctors', component: DoctorsComponent,
    children: [
      {path: 'add_doctor', component: AddDoctorComponent},
      {path: ':id', component: DoctorComponent, children: []},
    ],
  },

  {path: '**', redirectTo: 'doctors'},
]

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
