import {Injectable} from '@angular/core';
import {IAccessTokenInterface} from "../models";
import {environment} from "../../environments/environment";

const ACCESS_TOKEN = environment.ACCESS_TOKEN
const REFRESH_TOKEN = environment.REFRESH_TOKEN

@Injectable({
  providedIn: 'root'
})
export class TokenStorageService {

  constructor() {
  }

  signOut(): void {
    window.sessionStorage.clear()
  }

  public saveTokens(token:IAccessTokenInterface):void{
    window.sessionStorage.removeItem(ACCESS_TOKEN)
    window.sessionStorage.removeItem(REFRESH_TOKEN)
    window.sessionStorage.setItem(ACCESS_TOKEN, token.access)
    window.sessionStorage.setItem(REFRESH_TOKEN, token.refresh)
  }

  public getAccessToken():string{
    return window.sessionStorage.getItem(ACCESS_TOKEN) as string
  }
  public getRefreshToken():string{
    return window.sessionStorage.getItem(REFRESH_TOKEN) as string
  }


}
