import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Observable, tap} from "rxjs";
import {IAccessTokenInterface} from "../models";
import {base_urls} from "../constants/base_urls";
import {IUser} from "../models";
import {TokenStorageService} from "./token-storage.service";

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  ACCESS_TOKEN = 'auth-token'

  // REFRESH_TOKEN = 'refresh-token'

  constructor(
    private httpClient: HttpClient,
    private tokenStorage: TokenStorageService,
  ) {
  }



  login(user: IUser): Observable<IAccessTokenInterface> {
    return this.httpClient.post<IAccessTokenInterface>(`${base_urls.login}`, user)
  }

  register(credentials: {}): Observable<any> {
    return this.httpClient.post<any>(`${base_urls.users}`, {...credentials})
  }

  refreshToken(): Observable<IAccessTokenInterface> {
    const refresh = this.tokenStorage.getRefreshToken();
    return this.httpClient.post<IAccessTokenInterface>(`${base_urls.login}/refresh`, {refresh}).pipe(
      tap((tokens: IAccessTokenInterface) => {
        this.tokenStorage.saveTokens(tokens)
      })
    )
  }

  isAuthorization(): boolean {
    return !!window.sessionStorage.getItem(this.ACCESS_TOKEN)
  }
}
