import {Injectable} from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor, HttpErrorResponse
} from '@angular/common/http';
import { catchError , Observable, switchMap, throwError} from 'rxjs';
import {TokenStorageService} from "../token-storage.service";
import {AuthService} from "../auth.service";
import {Router} from "@angular/router";
import {IAccessTokenInterface} from "../../models";
import {Is_authenticated} from "../is_authenticated";

@Injectable()
export class AuthInterceptor implements HttpInterceptor {


  isRefreshing = false

  constructor(
    private authService: AuthService,
    private tokenStorage: TokenStorageService,
    private router: Router,
    private is_Auth_obs: Is_authenticated
  ) {
  }

  intercept(request: HttpRequest<unknown>, next: HttpHandler): Observable<HttpEvent<unknown>> {
    const isAuth = this.authService.isAuthorization()

    if (isAuth) {
      request = this.addToken(request, this.tokenStorage.getAccessToken())
      this.is_Auth_obs.userIsLogged.next(true)
    }else{
      this.router.navigate(['login']).then()
    }
    return next.handle(request).pipe(

      catchError((res: HttpErrorResponse) => {
        if (res && res.error && res.status === 401) {
          return this.handle401Error(request, next)
        }
        return throwError(() => {
          new Error('Token is expired')
          this.is_Auth_obs.userIsLogged.next(false)
        })
      })
    ) as any;
  }

  addToken(request: HttpRequest<any>, token: string): HttpRequest<any> {
    return request.clone({
      setHeaders: {Authorization: `Bearer ${token}`}
    })
  }

  handle401Error(request: HttpRequest<any>, next: HttpHandler): any {
    if (!this.isRefreshing) {
      this.isRefreshing = true;
      return this.authService.refreshToken().pipe(
        switchMap((tokens: IAccessTokenInterface) => {
          return next.handle(this.addToken(request, tokens.access))
        }),
        catchError(() => {
          this.isRefreshing = false
          this.tokenStorage.signOut();
          this.router.navigate(['login']).then()
          return throwError(() => new Error('token invalid or expired'))
        })
      )
    }
  }
}
